import { createStore } from 'vuex'

export default createStore({


  state: {
    arrayFull: [],
    array: [],
    arrayAverias: [],
    arraySoluciones: [],
    arrayTecnicos: [],
  },

  mutations: {
    setArray(state, payload) {
      state.arrayFull = payload.data;
      state.array = payload.data;
      state.arrayAverias = payload.averias;
      state.arraySoluciones = payload.soluciones;
      state.arrayTecnicos = payload.tecnicos;
    }
  },

  actions: {

    // OBTIENE DATOS DEL ARRAY
    async getArray({ commit }, array) {
      try {
        const res = await fetch(`../apis/gift.php`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();
        if (data.response == "SUCCESS") {
          commit("setArray", data);
          return { response: true, data: data };
        }
        return { response: false, data: [] };
      } catch (error) {
        console.log(error);
        return { response: false, data: [] };
      }
    },

    // OBTIENE DATOS DEL ARRAY
    async accionesUsuario({ commit }, array) {
      try {
        const res = await fetch(`../apis/gift.php`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();
        if (data.response == "SUCCESS") {
          return { response: true, data: data };
        }
        return { response: false, data: [] };
      } catch (error) {
        console.log(error);
        return { response: false, data: [] };
      }
    },

    // OBTIENE DATOS DEL ARRAY
    async sesion({ commit }, array) {
      try {
        const res =
          await fetch(`../apis/inicio_sesion.php`, {
            method: "POST",
            body: JSON.stringify(array),
          });
        const data = await res.json();
        if (data.response == "SUCCESS") {
          return { response: true, data: data };
        }
        return { response: false, data: [] };
      } catch (error) {
        console.log(error);
        return { response: false, data: [] };
      }
    },

  },

  modules: {
  }

})
