<template>
  <div
    class="
      w-full
      h-screen
      bg-gris-30
      flex flex-col
      items-center
      justify-start
      overflow-y-auto
    "
  >
    <div class="p-4 font-bold uppercase">
      <h1>Gráficos Averias</h1>
    </div>

    <div
      class="
        w-full
        p-4
        flex flex-row flex-wrap
        items-end
        justify-center
        pb-4
        sticky
        top-0
        bg-gris-30
        z-50
      "
    >
      <div
        class="
          font-bold
          flex flex-col
          items-center
          justify-center
          w-1/2
          md:w-64
          flex-none
          px-2
          md:mb-0
          mb-4
        "
      >
        <h1>Fecha inicio</h1>
        <input
          type="date"
          class="p-2 bg-white rounded shadow focus:outline-none w-full"
          v-model="fecha.fechaInicial"
        />
      </div>
      <div
        class="
          font-bold
          flex flex-col
          items-center
          justify-center
          w-1/2
          md:w-64
          flex-none
          px-2
          md:mb-0
          mb-4
        "
      >
        <h1>Fecha fin</h1>
        <input
          type="date"
          class="p-2 bg-white rounded shadow focus:outline-none w-full"
          v-model="fecha.fechaFinal"
        />
      </div>
      <div
        class="
          font-bold
          flex flex-col
          items-center
          justify-center
          w-full
          md:w-64
          flex-none
          px-2
          md:mb-0
          mb-4
        "
      >
        <button
          class="p-2 bg-blue-300 rounded shadow focus:outline-none w-full"
          @click="obtenerGrafica()"
        >
          Actualizar
        </button>
      </div>
    </div>

    <div class="w-full flex flex-row items-center justify-center"></div>

    <div class="" v-for="(item, index) in array" :key="index">
      <p
        class="font-bold p-2 mr-3 ml-3 bg-white rounded shadow"
        v-if="item.tecnicos.length || item.graficas.length"
      >
        {{ item.hotel }}
      </p>

      <div
        class="flex flex-col md:flex-row items-start justify-center w-full p-4"
      >
        <div
          class="p-4 rounded-xl bg-white shadow mr-4 md:w-1/2 w-full mb-6"
          v-if="item.tecnicos.length"
        >
          <h1 class="font-bold uppercase text-lg mb-4">
            Tecnicos sin comentarios
          </h1>
          <div
            class="
              flex
              items-center
              justify-between
              py-2
              hover:bg-blue-100
              px-3
              rounded-lg
              cursor-pointer
              font-bold
            "
          >
            <div class="w-3/4 text-left">
              <h1>TECNICO</h1>
            </div>
            <div class="w-1/4">
              <h2>AVERÍAS</h2>
            </div>
          </div>
          <div
            v-for="(item, index) in item.tecnicos"
            :key="index"
            class="
              flex
              items-center
              justify-between
              py-2
              hover:bg-blue-100
              px-3
              rounded-lg
              cursor-pointer
            "
          >
            <div class="w-3/4 text-left">
              <h1>{{ item.tecnico }}</h1>
            </div>
            <div class="w-1/4 font-bold">
              <h2>{{ item.total }}</h2>
            </div>
          </div>
        </div>

        <!-- GRAFICAS -->
        <div
          class="flex flex-row flex-wrap items-center justify-around"
          v-if="item.graficas.length"
        >
          <GraficosGift
            v-for="(item, index) in item.graficas"
            :key="index"
            :array="item"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import GraficosGift from "@/components/GraficosGift.vue";
import { computed, onMounted, ref } from "@vue/runtime-core";
import { useStore } from "vuex";
import { useRoute } from "vue-router";

export default {
  components: { GraficosGift },

  setup() {
    const store = useStore();
    const route = useRoute();
    const fechaHoy = new Date();

    const fecha = ref({
      fechaInicial:
        fechaHoy.getFullYear() +
        "-" +
        (fechaHoy.getMonth() + 1 > 9
          ? fechaHoy.getMonth() + 1
          : "0" + (fechaHoy.getMonth() + 1)) +
        "-" +
        fechaHoy.getDate(),
      fechaFinal:
        fechaHoy.getFullYear() +
        "-" +
        (fechaHoy.getMonth() + 1 > 9
          ? fechaHoy.getMonth() + 1
          : "0" + (fechaHoy.getMonth() + 1)) +
        "-" +
        fechaHoy.getDate(),
    });

    onMounted(async () => {
      await obtenerGrafica();
    });

    const obtenerGrafica = async () => {
      await store.dispatch("getArray", {
        idDestino: route.params.idDestino,
        idUsuario: localStorage.getItem("usuario"),
        action: "graficas1",
        fechaInicial: fecha.value.fechaInicial,
        fechaFinal: fecha.value.fechaFinal,
      });
    };

    const array = computed(() => {
      return store.state.array;
    });

    const arrayTecnicos = computed(() => {
      return store.state.arrayTecnicos;
    });

    return { obtenerGrafica, array, arrayTecnicos, fecha };
  },
};
</script>

<style scoped></style>
