<template>
  <transition
    enter-active-class="animate__animated animate__bounceIn"
    leave-active-class="animate__animated animate__bounceOut"
  >
    <div v-if="visible" class="registro relative">
      <div class="w-1/4">
        <h1 class="truncate text-xxs md:text-base">{{ array.idGift }}</h1>
      </div>
      <span class="mx-2 font-bold">-</span>
      <div class="w-1/4">
        <h1 class="truncate text-xxs md:text-base">{{ array.averia }}</h1>
      </div>
      <span class="mx-2 font-bold">-</span>
      <div class="w-1/4">
        <h1 class="truncate text-xxs md:text-base">{{ array.solucion }}</h1>
      </div>
      <span class="mx-2 font-bold">-</span>
      <div class="w-1/4">
        <h1 v-if="array.tecnico === ''">- - -</h1>
        <h1 v-else class="truncate text-xxs md:text-base">
          {{ array.tecnico }}
        </h1>
      </div>
      <div class="absolute right-0 mr-4">
        <button class="eliminar" @click="eliminarRegistro(array)">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="w-4 h-4"
            viewBox="0 0 24 24"
            fill="none"
            stroke="currentColor"
            stroke-width="2"
            stroke-linecap="round"
            stroke-linejoin="round"
          >
            <polyline points="3 6 5 6 21 6"></polyline>
            <path
              d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"
            ></path>
            <line x1="10" y1="11" x2="10" y2="17"></line>
            <line x1="14" y1="11" x2="14" y2="17"></line>
          </svg>
        </button>
      </div>
    </div>
  </transition>
</template>
<script>
import { ref } from "@vue/reactivity";
import { useStore } from "vuex";
export default {
  props: {
    array: { type: Object, required: true },
  },

  setup() {
    const store = useStore();
    const Swal = require("sweetalert2");
    const visible = ref(true);

    const eliminarRegistro = async (array) => {
      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "eliminarRegistro",
        idRegistro: array.idRegistro,
      });

      if (!response.response)
        Swal.fire({
          title: "REGISTRO NO ELIMINADO",
          html: `No se Elimino el Registro, Intente de Nuevo!`,
          icon: "info",
          background: "#2c3e50",
          customClass: {
            popup: "colored-toast",
            backdrop: "swal2-backdrop-show",
            icon: "swal2-icon-show",
          },
          iconColor: "#fff",
        });

      if (response.response) visible.value = false;
    };

    return { eliminarRegistro, visible };
  },
};
</script>
<style scoped>
.registro {
  @apply flex flex-row items-center justify-start text-sm bg-white hover:bg-blue-100 cursor-pointer py-4 px-3 shadow w-full border-b;
}

.registro:first-child {
  @apply rounded-t-xl;
}
.registro:last-child {
  @apply rounded-b-xl border-b-0;
}

.registro:hover .eliminar {
  display: flex;
}
.eliminar {
  display: hidden;
  @apply w-6 h-6 rounded-full bg-red-200 text-red-500 items-center justify-center hidden focus:outline-none;
}
</style>
