<template>
  <div
    class="
      w-full
      h-screen
      bg-gris-30
      flex flex-col
      items-center
      justify-start
      overflow-y-auto
    "
  >
    <div class="p-4 font-bold uppercase">
      <h1>Feedback Averías</h1>
    </div>

    <div
      class="
        w-full
        p-4
        flex flex-row flex-wrap
        items-end
        justify-center
        pb-4
        sticky
        top-0
        bg-gris-30
        z-50
      "
    >
      <!-- SELECT HOTEL -->
      <div
        class="
          font-bold
          flex flex-col
          items-center
          justify-center
          w-1/2
          md:w-64
          flex-none
          px-2
          md:mb-0
          mb-4
        "
      >
        <h1>Hotel</h1>
        <select
          v-model="registro.idHotel"
          class="p-2 bg-white rounded shadow focus:outline-none w-full"
        >
          <option value="0">Seleccione Hotel</option>
          <option
            v-for="(item, index) in arrayHoteles"
            :key="index"
            :value="item.idHotel"
          >
            {{ item.hotel }}
          </option>
        </select>
      </div>

      <!-- INPUT ID AVERÍA -->
      <div
        class="
          font-bold
          flex flex-col
          items-center
          justify-center
          w-1/2
          md:w-64
          flex-none
          px-2
          md:mb-0
          mb-4
        "
      >
        <h1>ID de GIFT</h1>
        <input
          v-model="registro.idGift"
          class="p-2 bg-white rounded shadow focus:outline-none w-full"
          placeholder="ID DE LA AVERÍA"
        />
      </div>

      <!-- SELECT AVERÍA -->
      <div
        class="
          font-bold
          flex flex-col
          items-center
          justify-center
          w-1/2
          md:w-64
          flex-none
          px-2
          md:mb-0
          mb-4
        "
      >
        <h1>Avería</h1>
        <select
          v-model="registro.idAveria"
          class="p-2 bg-white rounded shadow focus:outline-none w-full"
          @change="obtenerSoluciones(registro.idAveria)"
        >
          <option value="">Seleccione Avería</option>
          <option
            v-for="(item, index) in arrayAverias"
            :key="index"
            :value="item.idAveria"
          >
            {{ item.averia }}
          </option>
        </select>
      </div>

      <!-- SELECT SOLUCIÓN -->
      <div
        class="
          font-bold
          flex flex-col
          items-center
          justify-center
          w-1/2
          md:w-64
          flex-none
          px-2
          md:mb-0
          mb-4
        "
      >
        <h1>Solución</h1>
        <select
          v-model="registro.idSolucion"
          class="p-2 bg-white rounded shadow focus:outline-none w-full"
        >
          <option value="">Seleccione una solución</option>
          <option
            value="1kswdwcd41kswdwcd5"
            class="font-bold text-red-400 bg-red-100 py-2"
          >
            SIN COMENTARIO ÚTIL
          </option>
          <option
            v-for="(item, index) in arraySoluciones"
            :key="index"
            :value="item.idSolucion"
          >
            {{ item.solucion }}
          </option>
        </select>
      </div>

      <!-- SELECT TÉCNICO -->
      <div
        class="
          font-bold
          flex flex-col
          items-center
          justify-center
          w-1/2
          md:w-64
          flex-none
          px-2
          md:mb-0
          mb-4
        "
      >
        <h1>Técnico</h1>
        <select
          v-model="registro.idTecnico"
          class="p-2 bg-white rounded shadow focus:outline-none w-full"
        >
          <option value="1kswead8a1kswead8b">Seleccione técnico</option>
          <option
            v-for="(item, index) in arrayTecnicos"
            :key="index"
            :value="item.idTecnico"
          >
            {{ item.tecnico }}
          </option>
        </select>
      </div>

      <!-- BOTÓN AÑADIR -->
      <div
        class="
          font-bold
          flex flex-col
          items-center
          justify-center
          w-full
          md:w-64
          flex-none
          px-2
          md:mb-0
          mb-4
        "
      >
        <button
          @click="crearRegistro()"
          class="p-2 bg-green-300 rounded shadow focus:outline-none w-full"
        >
          Añadir
        </button>
      </div>
    </div>

    <div class="relative text-gray-600 py-1">
      <input
        type="search"
        placeholder="Buscar Registro"
        class="
          w-72
          bg-white
          h-10
          px-5
          pr-10
          rounded-full
          text-sm
          focus:outline-none
          font-semibold
          uppercase
        "
        v-model="palabra"
        @keyup="buscar(palabra)"
      />
      <button class="absolute right-0 top-0 mt-3 mr-4">
        <svg
          class="h-4 w-4 fill-current"
          xmlns="http://www.w3.org/2000/svg"
          xmlns:xlink="http://www.w3.org/1999/xlink"
          version="1.1"
          x="0px"
          y="0px"
          viewBox="0 0 56.966 56.966"
          style="enable-background: new 0 0 56.966 56.966"
          xml:space="preserve"
          width="512px"
          height="512px"
        >
          <path
            d="M55.146,51.887L41.588,37.786c3.486-4.144,5.396-9.358,5.396-14.786c0-12.682-10.318-23-23-23s-23,10.318-23,23  s10.318,23,23,23c4.761,0,9.298-1.436,13.177-4.162l13.661,14.208c0.571,0.593,1.339,0.92,2.162,0.92  c0.779,0,1.518-0.297,2.079-0.837C56.255,54.982,56.293,53.08,55.146,51.887z M23.984,6c9.374,0,17,7.626,17,17s-7.626,17-17,17  s-17-7.626-17-17S14.61,6,23.984,6z"
          />
        </svg>
      </button>
    </div>

    <div
      class="w-full md:w-1/2 flex flex-col items-center justify-start px-4 py-6"
    >
      <!-- FILA DE REGISTRO -->
      <RegistroGift
        v-for="(item, index) in arrayRegistros"
        :key="index"
        :array="item"
        class="w-full truncate"
      />
      <!-- FILA DE REGISTRO -->
    </div>
  </div>
</template>

<script>
import { useStore } from "vuex";
import { computed, ref } from "@vue/reactivity";
import RegistroGift from "@/components/RegistroGift.vue";
import uniqid from "uniqid";
import { onMounted } from "@vue/runtime-core";
import router from "@/router";

export default {
  components: { RegistroGift },
  props: {},

  setup() {
    const store = useStore();
    const Swal = require("sweetalert2");

    const registro = ref({
      idHotel: 0,
      idGift: "",
      idAveria: "",
      idSolucion: "",
      idTecnico: "1kswead8a1kswead8b",
    });

    const arrayHoteles = ref([]);

    const palabra = ref("");

    onMounted(async () => {
      const sesion = await store.dispatch("sesion", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "comprobarSesion",
      });

      if (!sesion.response) {
        router.push("/login");
        return;
      }

      await store.dispatch("getArray", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "registros",
      });

      obtenerHoteles();
    });

    const crearRegistro = async () => {
      if (registro.value.idHotel == 0) {
        Swal.fire({
          title: "AÑADA HOTEL",
          html: `Se requiere seleccionar el Hotel`,
          icon: "info",
          background: "#2c3e50",
          customClass: {
            popup: "colored-toast",
            backdrop: "swal2-backdrop-show",
            icon: "swal2-icon-show",
          },
          iconColor: "#fff",
        });
        return;
      }

      if (!registro.value.idGift.trim().length) {
        Swal.fire({
          title: "AÑADA EL ID",
          html: `Se requiere el ID de GIFT`,
          icon: "info",
          background: "#2c3e50",
          customClass: {
            popup: "colored-toast",
            backdrop: "swal2-backdrop-show",
            icon: "swal2-icon-show",
          },
          iconColor: "#fff",
        });
        return;
      }

      if (!registro.value.idAveria.trim().length) {
        Swal.fire({
          title: "SELECCIONE UNA AVERÍA",
          html: `Se requiere seleccionar una avería`,
          icon: "info",
          background: "#2c3e50",
          customClass: {
            popup: "colored-toast",
            backdrop: "swal2-backdrop-show",
            icon: "swal2-icon-show",
          },
          iconColor: "#fff",
        });
        return;
      }

      if (!registro.value.idSolucion.length) {
        Swal.fire({
          title: "SELECCIONE UNA SOLUCIÓN",
          html: `Se requiere seleccionar una Solución`,
          icon: "info",
          background: "#2c3e50",
          customClass: {
            popup: "colored-toast",
            backdrop: "swal2-backdrop-show",
            icon: "swal2-icon-show",
          },
          iconColor: "#fff",
        });
        return;
      }

      if (
        registro.value.idTecnico == "1kswead8a1kswead8b" &&
        registro.value.idSolucion == "1kswdwcd41kswdwcd5"
      ) {
        Swal.fire({
          title: "SELECCIONE UN TÉCNICO",
          html: `Se requiere seleccionar un Técnico`,
          icon: "info",
          background: "#2c3e50",
          customClass: {
            popup: "colored-toast",
            backdrop: "swal2-backdrop-show",
            icon: "swal2-icon-show",
          },
          iconColor: "#fff",
        });
        return;
      }

      const response = await store.dispatch("accionesUsuario", {
        idUsuario: localStorage.getItem("usuario"),
        idDestino: localStorage.getItem("idDestino"),
        action: "crearRegistro",
        idRegistro: uniqid() + uniqid(),
        idHotel: registro.value.idHotel,
        idGift: registro.value.idGift,
        idAveria: registro.value.idAveria,
        idSolucion: registro.value.idSolucion,
        idTecnico: registro.value.idTecnico,
      });

      if (!response.response)
        Swal.fire({
          title: "Registro NO Agregado!",
          html: `No se puedo agregar el registro`,
          icon: "info",
          background: "#2c3e50",
          customClass: {
            popup: "colored-toast",
            backdrop: "swal2-backdrop-show",
            icon: "swal2-icon-show",
          },
          iconColor: "#fff",
        });

      if (response.response && response.data.data) {
        store.state.array.push(response.data.data);
        registro.value.idGift = "";
        registro.value.idAveria = "";
        registro.value.idSolucion = "";
        registro.value.idTecnico = "1kswead8a1kswead8b";
      }
    };

    const arrayRegistros = computed(() => {
      return store.state.array;
    });

    const obtenerHoteles = async () => {
      const response = await store.dispatch("accionesUsuario", {
        idUsuario: localStorage.getItem("usuario"),
        idDestino: localStorage.getItem("idDestino"),
        action: "hoteles",
      });

      if (response.response) return (arrayHoteles.value = response.data.data);

      return (arrayHoteles.value = []);
    };

    const arrayAverias = computed(() => {
      return store.state.arrayAverias;
    });

    const arraySoluciones = computed(() => {
      return store.state.arraySoluciones;
    });

    const arrayTecnicos = computed(() => {
      return store.state.arrayTecnicos;
    });

    const obtenerSoluciones = async (idAveria) => {
      const response = await store.dispatch("accionesUsuario", {
        idUsuario: localStorage.getItem("usuario"),
        idDestino: localStorage.getItem("idDestino"),
        action: "obtenerSoluciones",
        idAveria: idAveria,
      });

      if (response.response) {
        store.state.arraySoluciones = response.data.data;
        registro.value.idSolucion = "";
      }
    };

    const buscar = (palabra) => {
      const array = store.state.arrayFull;

      // Vacio
      if (!palabra.length) {
        store.state.array = array;
        return;
      }

      // Con Texto
      if (palabra.length) {
        store.state.array = [];

        array.forEach((item) => {
          if (item.idGift.indexOf(palabra) > -1) {
            store.state.array.push(item);
          }
        });
      }
    };

    return {
      arrayRegistros,
      arrayHoteles,
      registro,
      crearRegistro,
      obtenerSoluciones,
      arrayAverias,
      arraySoluciones,
      arrayTecnicos,
      palabra,
      buscar,
    };
  },
};
</script>
<style scoped></style>
